import { Button } from "@material-ui/core";
import React from "react";
import { useHistory } from "react-router-dom";

function Wallet({localWallet}) {
  const history=useHistory();
  return (
    <div>
      <Button
        variant="contained"
        color="primary"
        onClick={() =>
          window.open(
            "https://payu.in/web/EC9C27781886F2CC7BB1E74638779705",
            "_self"
          )
        }
      >
        Add money to your wallet using PayU SandBox
      </Button><br/><br/>
        <Button variant="contained" color="primary" onClick={()=>history.push("/localwallet")}>
          Add Money to Your wallet by Local
        </Button><br/><br/>
        <h1>Wallet Balance</h1>
        <h2>Rs.{localWallet.totalAmount}</h2>
    </div>
  );
}

export default Wallet;
