import {
  Breadcrumbs,
  Button,
  FormLabel,
  TextField,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import { Link } from "react-router-dom";

function LocalWallet({ localWallet, setLocalWallet }) {
  const [isPayed, setIsPayed] = useState(false);
  return (
    <div className="App">
      <h1>
        <Breadcrumbs>
          <Link to="/">Home</Link>

          <Typography color="textPrimary">Profile</Typography>
        </Breadcrumbs>
      </h1>
      {!isPayed && (
        <div>
          <h1>Payment Details</h1>
          <FormLabel>Enter Amount to pay</FormLabel>
          <br />
          <br />
          <TextField
            variant="outlined"
            type="number"
            value={localWallet.amount}
            onChange={(e) =>
              setLocalWallet({ ...localWallet, amount: e.target.value })
            }
            placeholder="0.00"
          />
          <br />
          <br />
          <FormLabel>Phone Number</FormLabel>
          <br />
          <br />
          <TextField
            variant="outlined"
            value={localWallet.phoneno}
            onChange={(e) =>
              setLocalWallet({ ...localWallet, phoneno: e.target.value })
            }
            placeholder="Enter Phone Number"
          />
          <br />
          <br />
          <FormLabel>Email</FormLabel>
          <br />
          <br />
          <TextField
            variant="outlined"
            type="email"
            value={localWallet.email}
            onChange={(e) =>
              setLocalWallet({ ...localWallet, email: e.target.value })
            }
            placeholder="Enter Your Email"
          />
          <br />
          <br />
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              setIsPayed(true);
              setLocalWallet({
                ...localWallet,
                totalAmount: localWallet.totalAmount + localWallet.amount * 1,
                amount:0,
                phoneno:null,
                email:null,
              });
            }}
          >
            Pay Now
          </Button>
        </div>
      )}
      {isPayed && (
        <div>
          <h1>Payment Finished</h1>
          <Link to="/">
            <Button variant="contained" color="primary">
              Back to home page
            </Button>
          </Link>
        </div>
      )}
    </div>
  );
}

export default LocalWallet;
